<template>
  <div class="row">
    <div class="col-sm-12 col-md-5">
      <div
        id="simple-table_info"
        class="dataTables_info"
        role="status"
        aria-live="polite"
      >
        Показано с {{ fullCount }} по {{ firstRecord }} из {{ lastRecord }} записей
      </div>
    </div>
    <div class="col-sm-12 col-md-7">
      <div
        id="payment-request_paginate"
        class="dataTables_paginate paging_simple_numbers"
      >
        <paginate
          v-if="totalPages > 1"
          v-model="page"
          :page-count="totalPages"
          prev-text="Пред."
          next-text="След."
          container-class="pagination"
          page-class="paginate_button page-item"
          page-link-class="page-link"
          prev-link-class="page-link"
          next-link-class="page-link"
          prev-class="paginate_button page-item previous"
          next-class="paginate_button page-item next"
          :hide-prev-next="true"
        />
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapActions, mapGetters, mapMutations, mapState,
} from 'vuex';

export default {
  name: 'BillAction',
  computed: {
    page: {
      get() {
        return this.$store.state.bill.page;
      },
      set(newValue) {
        this.setPage(newValue);
        this.getBills();
      },
    },
    ...mapState('bill', ['fullCount', 'totalPages']),
    ...mapGetters('bill', ['firstRecord', 'lastRecord']),
  },
  methods: {
    ...mapMutations('bill', ['setPage']),
    ...mapActions('bill', ['getBills']),
  },
};
</script>
